import React from "react";

import { Img, Text } from "components";

const FETechStackColumnglobe = ({item, locked, addToList, removeFromList, updateTech, className, addedList, multipleSelection, selectedTech,name,selectedName }) => {
  const updateAddedList = (item) => {
    const isItemAddedAlready = addedList?.find(elem => elem.Name === (item.Name))
    if(isItemAddedAlready){
        removeFromList(item);
        updateTech("");
    } 
    else{
    addToList(item);
    updateTech(item);
    }
  }
  return (
    <>
    {!locked ? 
      <div 
        className={`${ multipleSelection ? (addedList?.find(elem => elem['Name'] === name)? "bg-bluegray_906 border-[2px] border-solid border-teal_A400" : "bg-gray_901")
          :
          selectedName === name ? "bg-bluegray_906 border-[2px] border-solid border-teal_A400": "bg-gray_901"} ${className}}`}
          onClick = {() => {multipleSelection ? updateAddedList(item) : updateTech(item)}}
          >
        <Img
          src={item.Icon}
          className="h-[50px] w-[auto]"
          alt="globe"
        />
        <Text
          className="font-inter font-semibold text-gray_50 text-center w-[auto]"
          as="h6"
          variant="h6"
        >
          {name}
        </Text>
      </div> :
      <div 
        className={`${"bg-gray_901"} ${className}`}
        onClick = {() => {multipleSelection ? updateAddedList(item) : updateTech(item)}}
        >
      <div className="flex flex-row gap-[20px] items-start justify-between w-[50%]">
        <Img
          src={item.src}
          className= {`${locked ? "grayscale" : null} h-[70px] w-[70px]`}
          alt="location"
        />
        { locked ?  
        <Img
          src="images/img_lock_gray_300.svg"
          className="h-[24px] w-[24px]"
          alt="lock"
        /> : null }
        
      </div>
        
        <Text
          className={`${locked ? "text-gray_801" : "text-gray_50"} font-inter font-semibold text-center text-gray_50 w-[auto] mt-2`}
          as="h6"
          variant="h6"
        >
          {name}
        </Text>
      </div>
      }
    </>
  );
};

FETechStackColumnglobe.defaultProps = { 
  defaultname: "React",
  src: "images/img_globe_light_blue_300.svg",
  locked: false,
 };

export default FETechStackColumnglobe;
